import { Component, Listen, Element, h } from '@stencil/core';

@Component({
  tag: 'ks-mini-cart',
  styleUrl: 'miniCart.css'
})
export class MiniCart {
  @Element() root: HTMLElement;

  componentDidLoad() {
    this.PageHeight();
  }

  PageHeight() {
    const panel = document.querySelector("ks-navbar-panel .panel") as HTMLDivElement;
    if(panel) {
      panel.style.height = document.documentElement.offsetHeight - this.root.offsetHeight + "px";
      console.log(panel.style.height);
    }
  }

  @Listen('resize', { target: 'window' })
  resizeHandler() {
    this.PageHeight();
  }

  render() {

    return [
        <div class="arrow"></div>,
        <div class="content">Test</div>
    ];
  }
}
